<template>
  <div class="content">
    <roomInfo :data="this.getRespone.restInfo" />
    <section class="guestInfo" v-show="active == null">
      <div class="guestContent">
        <div class="title">包房客人信息</div>
        <div class="list">
          <div class="guestItem" v-for="(item,index) in this.getRespone.guestInfo" :key="index">
            <img v-show="item.sex == 1" src="@/assets/b.png" />
            <img v-show="item.sex == 2" src="@/assets/g.png" />
            <div class="right">
              <div class="line">
                <p>{{item.cardNum}}</p>
                <p>剩余时间：{{item.surplsTime}}</p>
              </div>
              <div class="line">
                <p class="projectName">{{item.projectName}}</p>
                <p>技师工号：{{item.mechaincNum}}</p>
              </div>
              <div class="line">
                <font size='0.8rem'>{{item.serviceTime}}</font>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
    <changeRestRoom :active='active' @changeAcitve='operationBtn()'/>
    <changeRoom :active='active' @changeAcitve='operationBtn()'/>
    <leaveRoom :active='active' @changeAcitve='operationBtn()'/>
    <cancelOpenRoom :active='active' @changeAcitve='operationBtn()'/>
    <section class="operationMean" v-show="active == null">
      <div
        class="mean"
        v-for="(item,index) in this.getMeanJson.spaOperationBtn"
        :key="index"
        @click="operationBtn(item.id)"
      >
        <img :src="item.imgIco" />
        <p>{{item.name}}</p>
      </div>
    </section>
  </div>
</template>

<script>
import roomInfo from "@/components/roomInfo";
import changeRestRoom from '@/components/freeForm/changeRestRoom'
import changeRoom from '@/components/restForm/changeRoom'
import leaveRoom from '@/components/restForm/leaveRoom'
import cancelOpenRoom from '@/components/restForm/cancelOpenRoom'

export default {
  data() {
    return {
      active: null
    };
  },
  methods: {
    operationBtn(id) {
      if (id != undefined) {
        this.active = id;
      } else {
        this.active = null;
      }
    }
  },
  components: {
    roomInfo,
    changeRestRoom,
    changeRoom,
    leaveRoom,
    cancelOpenRoom
  }
};
</script>

<style lang="scss" scoped>
.content {
  background: $F5;
  overflow: hidden;
  position: relative;
}
.operationMean {
  @include grid(4, auto);
  @include sc(0.9rem, $fColor);
  @include wh(100%, auto);
  position: fixed;
  bottom: 0rem;
  z-index: 2;
  background-color: $fc;
  .mean {
    background-color: $fc;
    padding: 0.5rem;
    img {
      @include wh(2.5rem, 2.5rem);
      border: 0.2rem solid $F5;
      border-radius: 5rem;
    }
  }
}
.guestInfo {
  padding: 0.5rem;
  z-index: 2;
  position: fixed;
  @include wh(100%, 100%);
  .guestContent {
    background-color: $fc;
    border-radius: 0.5rem;
    padding: 0.8rem 0.5rem;
    .title {
      @include sc(0.9rem, $fColor);
      font-weight: bold;
      text-align: left;
      border-bottom: 0.02rem solid $F5;
      padding-bottom: 0.5rem;
    }
    .list {
      @include wh(100%, 20rem);
      overflow: scroll;
      position: relative;
      z-index: 2;
      background-color: $fc;
      .guestItem {
        @include fr(flex-start);
        @include wh(100%, auto);
        border-bottom: 0.05rem solid $F5;
        margin: 0.5rem 0rem;
        padding-bottom: 0.5rem;
        img {
          @include wh(2.3rem, 2.3rem);
          margin-right: 0.5rem;
        }
        .right {
          @include fc(flex-start);
          @include wh(100%, auto);
        }
        .line:first-child {
          @include fr(space-between);
          font-weight: bold;
          p:last-child {
            @include sc(0.8rem, $line);
            font-weight: normal;
          }
        }
        .line {
          @include fr(flex-start);
          @include wh(100%, auto);
          @include sc(0.85rem, $fColor);
        }
        .line:last-child {
          @include sc(0.8rem, $line);
        }
        .line:nth-child(2) {
          @include sc(0.75rem, $fColor);
        }
        .projectName {
          margin-right: 1rem;
        }
      }
    }
  }
}

</style>